import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router/router'
import axios from "axios"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {  //    data
    isLogin: false,
    isLoginError: false,
    userInfo: null
  },
  mutations: { // state   

    //   
    loginSuccess(state, payload) {
      state.isLogin = true
      state.isLoginError = false
      state.userInfo = payload
    },

    //   
    loginError(state) {
      state.isLogin = false
      state.isLoginError = true
    },

    // 
    logout(state) {
      localStorage.removeItem("accessToken") // token 
      state.isLogin = false
      state.isLoginError = false
      state.userInfo = null
    }

  },
  actions: { //  
    //  
    doLogin({ dispatch }, loginObj) {
      //      .
      axios.post("http://localhost/login", loginObj)
        .then(res => {
          alert(" .")
          // alert(res.data.accessToken)
          //       
          localStorage.setItem("accessToken", res.data.accessToken)
          dispatch("getMemberInfo", true)
        })
        .catch(err => { // error code  

          // 401 ->   
          if(err.response.status === 401) {
            alert('  .')
            router.push({ name: "Home" })
          }

          if(err.response.status === 404) {
            alert('   .')
          }
        })
    },

    //  
    doLogout({ commit }) {
      commit("logout")
      alert("  .")
      router.push({ name: "Home" })
    },

    //  
    getMemberInfo({ commit }, isFirst) {
      let token = localStorage.getItem("accessToken")
      if (token != null) {

        const config = {
            headers: { Authorization: `Bearer ${token}` }
        };
        
        const bodyParameters = {
          key: "value"
        };

        axios
          .post("http://localhost/loginUser", bodyParameters, config)
          .then(response => {
              let userInfo = {
                id: response.data.id,
                name: response.data.name,
                email: response.data.email,
                phone: response.data.phone,
                address: response.data.address,
                auth: response.data.auth
              }
              commit("loginSuccess", userInfo)
              if(isFirst === true) {
                  router.push({ name: "Home" })
              }
            })
          .catch(error => {
            alert('   .')
            console.log(error)
          })
      }
    }
  }
})