<template>
<div></div>
</template>
<script>
import axios from "axios"
import router from "../router/router"
export default {

    beforeMount() {
      this.checkEmailAuth()
    },
    methods: {
        //          
        checkEmailAuth() {
            alert('gogo')
          axios.get("http://localhost/members/registerConfirm?email=" + this.$route.query.email
          + "&authKey=" + this.$route.query.authKey
          )
          .then(res => {
            if(res.status === 201) {
              alert(" .")
            }
            if(res.status === 208) {
              alert("   .")
            }            
            router.push({ name: "Home" })
          })
          .catch(() => {
            alert("  .")
            router.push({ name: "Home" })
          })
        },
    }
}
</script>
